import React, { useState } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
} from 'reactstrap';
import '../assets/testimonial.css';

const items = [
  {
    src:
      'https://images.pexels.com/photos/316466/pexels-photo-316466.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500',
    altText: 'Slide 1',
    caption:
      'Terapia obejmowała cały układ odpornościowy, ze szczególnym uwzględnieniem części lędźwiowej kręgosłupa, który bolał mnie od jakiegoś czasu. Terapia trwała 4 dni. Po zakończeniu cyklu terapeutycznego, w ciągu kilku kolejnych dni ustąpił ból kręgosłupa. Należy zaznaczyć, że w tym czasie nie zażywałam leków, ani też nie korzystałam z klasycznej fizykoterapii. Od tego czasu minęło kilka miesięcy - do dzisiaj nie odczuwam żadnych dolegliwości z tego tytułu.',
    tags: '#terapia na odległość',
  },
  {
    src:
      'https://images.pexels.com/photos/316466/pexels-photo-316466.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500',
    altText: 'Slide 2',
    caption:
      'Terapia miała być robiona z myślą o boreliozie. Ból nie do wytrzymania, ręce średnio sprawne.Po zakończeniu 4dniowej terapii przez ponad 2 miesiące nie miałam napadu bólu w stawach. Efekt dla mnie porażający. Z całego serca polecam, szczególnie ze względu na to, że terapeuci tej metody to ludzie niezwykle dobrzy, uważni, sympatyczni, nie zniszczeni rutyną i przede wszystkim czuli na potrzebę drugiego człowieka.',
    tags: '#borelioza',
  },
  {
    src:
      'https://images.pexels.com/photos/316466/pexels-photo-316466.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500',
    altText: 'Slide 3',
    caption:
      'Jaś, 2 latka. Często choruje (proste infekcje gardła, nieżyt z nosa, kaszel), trwa to zazwyczaj dość długo-kaszel utrzymuje się nawet do 2 miesięcy.(...) Przez ok miesiąc od terapii Jaśkowi nie przydarzyło się nic prócz pozostałości kaszelków w ciągu dnia.Szczególnie przy dzieciach ważne jest dla mnie szukanie alternatywnych rozwiązań niekonwencjonalnych. Dlatego z całego serca polecam terapię metodą Zdenka Domancica.',
    tags: '#terapia dziecka',
  },
];

const Testimonial = props => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  };

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  };

  const goToIndex = newIndex => {
    if (animating) return;
    setActiveIndex(newIndex);
  };
  const slides = items.map(item => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item.src}
      >
        <img src={item.src} alt={item.altText} />
        <CarouselCaption
          className="d-block"
          captionText={item.caption}
          captionHeader={item.tags}
        />
      </CarouselItem>
    );
  });

  return (
    <div className="carousel-container d-none d-sm-block">
      <h2 className="text-center">Referencje</h2>
      <Carousel activeIndex={activeIndex} next={next} previous={previous}>
        <CarouselIndicators
          items={items}
          activeIndex={activeIndex}
          onClickHandler={goToIndex}
        />
        {slides}
        <CarouselControl
          direction="prev"
          directionText="Previous"
          onClickHandler={previous}
        />
        <CarouselControl
          direction="next"
          directionText="Next"
          onClickHandler={next}
        />
      </Carousel>
    </div>
  );
};

export default Testimonial;
