//import base64 from 'base-64';

const send = form => {
    const url = 'http://localhost:3000/contact';
    //Iam using proxy due to CORS
    const proxyUrl = 'https://cors-anywhere.herokuapp.com/';

    //console.log(JSON.stringify(form));

    //const authHeader = { Authorization: 'Basic ' + base64.encode(u + ':' + p) };
    const otherHeaders = {
        Accept: 'application/json',
        'Content-type': 'application/json',
    };

    fetch(url, {
        method: 'POST',
        headers: Object.assign(otherHeaders, {}),
        body: JSON.stringify(form),
        mode: 'cors',
    }).then(resp => {
        if (resp.status === 200) {
            return resp.json(), alert('Twoja wiadomość została wysłana!');
        } else {
            console.log(resp);
            return resp, alert('Wystąpił błąd wysyłania ');
        }
    });
};

export default send;