import React from 'react';
import Layout from '../components/Layout';
import Header from '../components/Header';
import Scroll from '../components/Scroll';
import Footer from '../components/Footer';
import Social from '../components/Social';
import Testimonial from '../components/Testimonial';
import Certificate from '../components/Certificate';
import demo1 from '../assets/images/demo-screen.jpg';
import CalendlyEmbed from '../components/CalendlyEmbed';
import MoreAboutMe from '../components/MoreAboutMe';
import DiseaseList from '../components/DiseaseList';

const IndexPage = () => (
  <Layout>
    <Header />
    <header className="masthead">
      <div className="container h-100">
        <div className="header-content mx-auto text-justify">
          <div className="button">
            <Scroll type="id" element="features">
              <a href="#features" className="btn btn-outline btn-xl">
                Dowiedz się więcej
              </a>
            </Scroll>
          </div>
        </div>
      </div>
      <div className="overlay"></div>
    </header>

    <section className="download bg-primary text-center" id="aboutMe">
      <div className="container">
        <div className="row">
          <div className="col-md-12 mx-auto">
            <h2 className="section-heading">O mnie</h2>
            <p className="text-justify">
              Ukończyłam studia pedagogiczne na kierunku Pedagogika
              Opiekuńczo-Socjalna. Przez wiele lat pracowalam jako instruktor
              jazdy konnej i hipoterapeuta. Moja przygoda z bioterapią
              rozpoczęła się od potrzeby niesienia pomocy najbliższym. Jednak
              szybko zrozumiałam, że to moja droga życiowa (już w pierwszym dniu
              nauki wiedziałam, że będę prowadzić terapie grupowe). Ukończyłam
              kursy 1 i 2 stopnia Metody Zdenko Domancica. Posiadam tytuł
              czeladniczy z zakresu Bioenergoterapii i Naturalnych Metod
              Uzdrawiania, uprawniający do wykonywania zawodu na terenie całej
              UE. Z ogromną przyjemnością pomagam przy kursach dla kolejnych
              adeptów uczących się Metody Zdenko Domancica. Wspaniale jest
              obserwować radość i dobre samopoczucie podopiecznych, a potem
              niejednokrotnie widzieć ich uczących się pracy z energią.
            </p>
            <p className="text-justify">
              Na co dzień pracuję w kontakcie bezpośrednim grupowo i
              indywidualnie. Mogę także zaproponować terapię, która nie zna
              granic, czyli bioenergoterapię na odległość. Z mojej pomocy
              korzystają ludzie, zwierzęta i rośliny. Za mną już wiele terapii,
              a ja nadal nie mogę wyjść z podziwu nad prostotą i skutecznością
              tej metody. Cieszę się, że dobro idzie dalej w świat.
            </p>
            <p className="text-justify">
              Zajmuję się także odczytami z Kronik Akaszy. Ukończyłam 1 i 2
              stopień tej metody u Gabrielle Orr.
            </p>
          </div>
        </div>
      </div>
    </section>

    <section className="features" id="features">
      <div className="container">
        <div className="section-heading text-justify">
          <h2 className="text-center">Bioenergoterapia</h2>
          <p className="text-muted text-justify">
            Bioterapia ma na celu wyrównanie stanu energetycznego całej istoty
            jaką jesteśmy (doprowadzenie do równowagi fizycznej, emocjonalnej,
            mentalnej i duchowej). Dzieje się to za pomocą energii, która niesie
            ze sobą doskonałą informację. Dysfunkcje pojawiają się najpierw w
            ciałach energetycznych (w aurze). Kiedy konflikt nie jest rozwiązany
            zaczynają się manifestować w ciele fizycznym (jakimś organie lub
            obszarze). Równowaga powoduje swobodny przepływ energii, pobudzenie
            systemu odporności, a to prowadzi do regeneracji i samouzdrowienia.
            Uzdrowienie lub zanikanie bólu jest spowodowane tym, że dotknięte
            obszary ciała powracają do równowagi. Jeżeli układ odpornościowy
            jest silny, wtedy organizm może wytworzyć każdą substancję jaka jest
            mu potrzebna i zwalczyć chorobę.W ten oto piękny sposób ludzkie
            ciało leczy się samo. Czysta, poinformowana energia krąży po ciele i
            dokonuje naprawy jeszcze w ciągu 2-3 tygodni od zakończenia
            zabiegów.
          </p>
          <hr />
          <h2 className="text-center">Bioenergia</h2>
          <p className="text-muted text-justify">
            Bioenergia jest podstawą życia- “bios” z greckiego “życie”. Inne
            nazwy siły życiowej to Chi, Prana, Ki, Mana, siła witalna, energia
            kosmiczna. Każda istota jest stworzeniem zarówno materialnym jak i
            energetycznym, a do życia potrzebuje Prany. Dopóki krąży ona
            swobodnie i ma jej w sobie wystarczająco dużo, zostaje zachowana
            równowaga i dobrostan. Kiedy energii życia brakuje lub nie jest ona
            równomiernie rozprowadzana – istota zaczyna chorować. Bioenergia
            zawiera doskonałą wiedzę na temat zdrowia i życia oraz inne
            zaawansowane informacje. Zawsze dociera do przyczyny problemu z
            jakim boryka się dana jednostka.
          </p>
          <hr />
        </div>
      </div>
    </section>

    <section className="download bg-primary text-center" id="aboutMethod">
      <div className="container">
        <div className="row">
          <div className="col-md-12 mx-auto">
            <h2 className="section-heading">O metodzie</h2>
            <p className="text-justify">
              Zdenko Domancic podczas ponad trzydziestoletniej praktyki uleczył
              prawie milion osób. Bioenergoterapia Metodą Zdenko Domancica, to
              najlepiej przebadana naukowo terapia energetyczna. Pragnąc naukowo
              zweryfikować swoją metodę, Domancic zwrócił się nie tylko do
              lekarzy, ale także do chemików, fizyków, cybernetyków i
              psychologów. W 1984 roku metodę wzięli pod lupę specjaliści z
              międzynarodowej komisji lekarskiej. Najbardziej spektakularną
              częścią tego badania było uzdrowienie 300 osób z gangreną suchą
              (rzekomą) i mokrą. Żadna z nich nie miała amputowanej kończyny.
              Ten ogromny sukces spowodował, że do kliniki w Krańskiej Górze
              (która jest dotowana przez władze Słowenii i patronuje jej
              Ministerstwo Zdrowia) przybywają ludzie z całego świata. Cześć z
              nich jest pacjentami, a część szkoli się w pracy z energią.
            </p>
            <p className="text-justify">
              Inne osiągnięcia Zdenko Domancica, to wspaniałe efekty
              terapeutyczne w takich chorobach jak: cukrzyca, Parkinson,
              Alzheimer, astma, artretyzm, żółtaczka typu C, nowotwory, udary i
              wiele innych. Otrzymał prestiżową nagrodę Prezydenta Słowenii za
              wyniki pracy. Uczestniczy w badaniach nad AIDS prowadzonych przez
              Uniwersytet Stanforda oraz w badaniach nad rakiem w Instytucie
              Onkologii w Lubljanie. W jego klinice leczy się wielu sławnych
              ludzi, w tym słoweńska reprezentacja sportowców olimpijskich.
              Wielu jego terapeutów ma wspaniałe wyniki w pracy z klientami.
              Osoby, które uczestniczą w terapiach bardzo chwalą tę metodę.
              Trafiła do ich serc o wiele wcześniej, niż została sprawdzona
              przez ekspertów medycyny akademickiej.
            </p>
            <p className="text-justify">
              Ujmująca jest prostota Metody Zdenko Domancica. Wyszkolony i
              doświadczony bioterapeuta pracuje ze zdrowiem. Powrót do zdrowia
              nie powoduje efektów ubocznych. Jest to metoda bezpieczna i
              komplementarna. Terapeuta Metody Domancica jest narzędziem
              przekazującym bioenergię pobieraną z otoczenia. Dzięki temu nie
              traci swojej energii życiowej.
            </p>
            <p className="text-justify">
              Oryginalność Metody Zdenko Domancica kryje się w sposobie
              pobierania i przenoszenia bioenergii na ciało osoby potrzebującej
              i jej równomiernym rozmieszczeniu w organizmie. Technika ta
              została przystosowana do cywilizacji zachodniej: sposobu
              rozumowania, życiowych poglądów, stylu życia, kultury.
            </p>
            <p className="text-justify">
              Może być uzupełnieniem innych terapii. Jest naturalna, bezbolesna
              i nieinwazyjna. Skutecznie poprawia stan fizyczny i silnie
              oddziałuje na psychikę wzmacniając ją. Nie ma ona nic wspólnego z
              jakąkolwiek religią.
            </p>
          </div>
        </div>
      </div>
    </section>

    <section className="text-center" id="aboutTherapy">
      <div className="container">
        <div className="row">
          <div className="col-md-12 mx-auto">
            <h2 className="section-heading">O terapii</h2>

            <div className="row">
              <div className="col-lg-12 my-auto">
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-lg-6">
                      <h3>Terapia grupowa</h3>
                      <p className="text-justify text-muted">
                        Podczas terapii grupowych współpracuję z innymi
                        terapeutami (2 terapeutów na jednego uczestnika
                        terapii). Są one zorganizowane w indywidualnym podejściu
                        do każdego uczestnika. W zależności od wielkości grupy
                        terapia taka trwa 1- 2 godzin. Zalecane jest przebywanie
                        w sali jak najdłuższą ilość czasu przed i po
                        indywidualnym zabiegu. Energia działa już w czasie
                        przebywania w pomieszczeniu, niezależnie od
                        indywidualnego zabiegu. Nie ma z góry określonej
                        kolejności, terapeuci proszą każdego intuicyjnie. Cały
                        zabieg dla jednej osoby trwa 15- 20 minut. Cały cykl
                        terapii trwa 4 dni z rzędu. Terapeuta łączy się z
                        energią wszechświata uruchamiając jej przekaz poprzez
                        odpowiednią sekwencję ruchową rąk, a uczestnik terapii
                        przyjmując ją inicjuje proces zdrowienia. Celem terapii
                        jest całkowite wyeliminowanie, zmniejszenie lub
                        zahamowanie problemów zdrowotnych.
                      </p>
                    </div>

                    <div className="col-lg-6">
                      <h3>Terapia indywidualna</h3>
                      <p className="text-justify text-muted">
                        Każdy z uczestników bioterapii jest zobowiązany do
                        kontaktu z lekarzem konwencjonalnym i stosowania się do
                        jego zaleceń. Nie powinien także bez konsultacji z
                        lekarzem przerywać przyjmowania leków lub zmniejszania
                        ich dawek. Uczestnicy bioterapii są zobowiązani do
                        zrobienia badań lekarskich i wcześniejszego zgłoszenia
                        diagnozy oraz objawów choroby (za pomocą formularza
                        kontaktowego). Bioterapeuci Metody Zdenko Domancica nie
                        zalecają żadnych środków farmakologicznych i nie
                        udzielają porad medycznych, nie sugerują także innych
                        metod komplementarnych, naturalnych itp. Metoda
                        uzdrawiania bioenergią, opracowana przez Zdenko
                        Domancica, nie stanowi zamiennika dla medycyny
                        konwencjonalnej, lecz jest jej uzupełnieniem. Medycyna
                        oficjalna jest partnerem dla uzdrawiania bioenergią, a
                        nie konkurencją. Bioenergoterapia jest świetnym
                        uzupełnieniem dla medycyny akademickiej.
                      </p>
                    </div>
                    <div className="col-lg-6">
                      <h3>Uwagi, zalecenia</h3>
                      <p className="text-justify text-muted">
                        Zabieg terapii indywidualnej trwa ok. 20-40 minut w
                        zależności od schorzenia i jest powtarzany przez 4 dni z
                        zrzędu. W czasie cyklu zabiegów nie powinno się
                        korzystać z innych terapii energetycznych, np.
                        akupunktura, terapia dźwiękiem, masaże itp.
                      </p>
                    </div>

                    <div className="col-lg-6">
                      <h3>Podziękowanie dla terapeuty</h3>
                      <p className="text-justify text-muted">
                        Podziękowanie dla terapeuty jest w ostatnim dniu terapii
                        w formie donacji od serca (w nieoznakowanych kopertach).
                        Uczestnik sam decyduje o wysokości wpłaty zgodnie ze
                        swoim samopoczuciem, możliwościami finansowymi i
                        sumieniem.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container text-center">
        <DiseaseList />
      </div>
    </section>

    <section className="testimonial" id="testimonial">
      <div className="container text-center">
        <Testimonial />
      </div>
    </section>

    <section className="contact bg-primary" id="contact">
      <div className="container">
        <Social />
      </div>
    </section>

    <section className="certificate" id="certificate">
      <div className="container text-center">
        <h2>Certyfikaty i szkolenia</h2>
        <Certificate />
      </div>
    </section>

    <Footer />
  </Layout>
);

export default IndexPage;

